$wine-red: #962013;
$vibrant-red: #C0392B;

.header {
    opacity: 1;
    z-index: 1000;
    top: 0;
    position: fixed;
    width: 100vw;
    height: 12vh;
    left: 0;
    padding: 0;
    margin: 0;
    transition: 1s all ease-in-out;
    .icons {
        padding: 0;
        z-index: 0;
        position: absolute;
        top: 5vh;
        display: flex;
        flex-direction: row;
        text-decoration: none;
        .icon {
            color: whitesmoke;
            margin: 0px 5px;
            margin: 0px 10px;
        }
    }
    .left {
        left: 5vw;
    }
    .right {
        right: 5vw;
        cursor: not-allowed;
    }
}
